input{
  border: none;
  box-shadow: 0px 0px 10px rgba(67,65,65,.4);
  font-size: 1.2rem;
  outline: none;
  padding: 10px;
  margin:20px;
  border: 2px solid transparent;
  border-radius: 10px;
}
input:focus{
  text-decoration: none;
}
input:focus-within{
  border:2px solid aqua;
}
.App{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
button{
  width: 150px;
  padding: 20px;
  margin: 10px;
  border: 2px solid whitesmoke;
  box-shadow: 0px 0px 10px rgba(67,65,65,.4);
  font-size: 1.2rem;
  border-radius: 200px;
  cursor: pointer;
}
button:active{
  color:red;
  background-color: antiquewhite;
}
td,th{
  text-align: center;
}